import React, { useState, useEffect } from 'react';
import RandomizeButton from './RandomizeButton';
import Court from './Court';

function Assignment({ 
  numTotCourts, 
  players, 
  setPlayers, 
  courts,
  setCourts,
  currentStartIndex, 
  updateStartIndex,
  isLocked, 
  specialPlayers,
  setSpecialPlayers,
  isSpecialEnabled
}) {
  const [selectedPlayers, setSelectedPlayers] = useState([]);
  const [temporaryCourts, setTemporaryCourts] = useState([]); //   
  const [tempStartIndex, setTempStartIndex] = useState(currentStartIndex.current); //   
  const [assignClicked, setAssignClicked] = useState(false); // Assign players  

  // :      
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [newPlayerName, setNewPlayerName] = useState('');
  const [selectedSinglePlayer, setSelectedSinglePlayer] = useState(null); //    

  // saving updated courts data on localStorage
  useEffect(() => {
    localStorage.setItem('courts', JSON.stringify(courts));
  }, [courts]);

  function onCourtSelected(courtIndex) {
    if (isLocked) return; 
    const updatedCourts = courts.map((court, i) => ({
      ...court,
      isSelected: i === courtIndex ? !court.isSelected : court.isSelected
    }));
    setCourts(updatedCourts);
  }

  //  :   
  function handlePlayerClick(player, courtIndex = null) {
    const alreadySelected = selectedPlayers.find((p) => p.id === player.id);
  
    if (alreadySelected) {
      //    
      setSelectedPlayers((prev) => prev.filter((p) => p.id !== player.id));
    } else {
      //    
      const firstSelectedCourtIndex =
        selectedPlayers.length > 0 ? selectedPlayers[0].courtIndex : null;
  
      //       
      if (firstSelectedCourtIndex === courtIndex) {
        //        
        setSelectedPlayers([{ ...player, courtIndex }]);
        return;
      }
  
      //          
      const sameCourtPlayerIndex = selectedPlayers.findIndex(
        (p) => p.courtIndex === courtIndex
      );
  
      if (sameCourtPlayerIndex !== -1) {
        //     ,   
        const updatedSelection = [...selectedPlayers];
        updatedSelection[sameCourtPlayerIndex] = { ...player, courtIndex };
        setSelectedPlayers(updatedSelection);
      } else {
        //    (     )
        const newSelection = [...selectedPlayers, { ...player, courtIndex }];
        if (newSelection.length > 2) {
          newSelection.shift(); // 2      
        }
        setSelectedPlayers(newSelection);
      }
    }
  }
  

  function onAssignPlayers(courtAssignments) {
    const updatedCourts = courts.map((court) => ({
      ...court,
      players: courtAssignments[court.courtIndex] || []
    }));
    //     (  )
    setTemporaryCourts(updatedCourts);
    setCourts(updatedCourts); //   
    setTempStartIndex(currentStartIndex.current); //   
    setAssignClicked(true); // Assign players  
  }

  function handleChangePlayers() {
    if (selectedPlayers.length === 1) {
      //      
      setSelectedSinglePlayer(selectedPlayers[0]); //   
      setIsModalOpen(true);
      return;
    }

    if (selectedPlayers.length !== 2) {
      alert('    .');
      return;
    }
  
    const [player1, player2] = selectedPlayers;
  
    setCourts((prevCourts) => {
      return prevCourts.map((court, index) => {
        if (index === player1.courtIndex) {
          // player1   player2 
          const player1Index = court.players.findIndex((p) => p.id === player1.id);
          const updatedPlayers = [...court.players];
          updatedPlayers[player1Index] = player2; // player1  player2 
          return {
            ...court,
            players: updatedPlayers
          };
        } else if (index === player2.courtIndex) {
          // player2   player1 
          const player2Index = court.players.findIndex((p) => p.id === player2.id);
          const updatedPlayers = [...court.players];
          updatedPlayers[player2Index] = player1; // player2  player1 
          return {
            ...court,
            players: updatedPlayers
          };
        }
        return court; //    
      });
    });
  
    //  
    setSelectedPlayers([]);
  }
  
  //  
  // //  Change Players    
  // function handleModalChange() {
  //   if (!newPlayerName.trim()) {
  //     alert(' .');
  //     return;
  //   }

  //   setCourts((prevCourts) =>
  //     prevCourts.map((court) => {
  //       if (court.courtIndex === selectedSinglePlayer.courtIndex) {
  //         const updatedPlayers = [...court.players];
  //         const playerIndex = updatedPlayers.findIndex((p) => p.id === selectedSinglePlayer.id);

  //         if (playerIndex !== -1) {
  //           updatedPlayers[playerIndex] = { ...selectedSinglePlayer, name: newPlayerName };
  //         }

  //         return { ...court, players: updatedPlayers };
  //       }
  //       return court;
  //     })
  //   );

  //   setIsModalOpen(false); //  
  //   setSelectedPlayers([]);
  // }

  //    
  // function handleModalChange() {
  //   if (!newPlayerName.trim()) {
  //     alert(' .');
  //     return;
  //   }
  
  //   const updatedCourts = courts.map((court) => {
  //     if (court.courtIndex === selectedSinglePlayer.courtIndex) {
  //       const updatedPlayers = [...court.players];
  //       const playerIndex = updatedPlayers.findIndex((p) => p.id === selectedSinglePlayer.id);
  
  //       if (playerIndex !== -1) {
  //         updatedPlayers[playerIndex] = { ...selectedSinglePlayer, name: newPlayerName };
  //       }
  
  //       return { ...court, players: updatedPlayers };
  //     }
  //     return court;
  //   });
  
  //   setCourts(updatedCourts); // courts 
  //   setTemporaryCourts(updatedCourts); // temporaryCourts 
  
  //   setIsModalOpen(false); //  
  //   setSelectedPlayers([]); //  
  // }
  
  function handleModalChange() {
    if (!newPlayerName.trim()) {
      alert(' .');
      return;
    }
  
    // courts : ID   
    const updatedCourts = courts.map((court) => {
      if (court.courtIndex === selectedSinglePlayer.courtIndex) {
        const updatedPlayers = court.players.map((player) =>
          player.id === selectedSinglePlayer.id
            ? { ...player, name: newPlayerName } //  
            : player
        );
        return { ...court, players: updatedPlayers };
      }
      return court;
    });
  
    setCourts(updatedCourts); // courts  
    setTemporaryCourts(updatedCourts); // temporaryCourts  
  
    // players : ID   
    const updatedPlayers = players.map((player) =>
      player.id === selectedSinglePlayer.id
        ? { ...player, name: newPlayerName } //  
        : player
    );
  
    setPlayers(updatedPlayers); // players  
    localStorage.setItem('players', JSON.stringify(updatedPlayers)); //  
    localStorage.setItem('courts', JSON.stringify(updatedCourts)); //  
  
    setIsModalOpen(false); //  
    setSelectedPlayers([]); //  
  }
  

  //  Cancel    
  function handleModalCancel() {
    setIsModalOpen(false); //  
    setSelectedPlayers([]);
  }

  // //    
  // function handleConfirmAssignments() {
  //   if (temporaryCourts.length === 0) {
  //     alert("Assign players first before confirming!");
  //     return;
  //   }
  
  //   //   (Confirmation )
  //   currentStartIndex.current =
  //     (currentStartIndex.current + courts.filter((court) => court.isSelected).length * 4) %
  //     players.length;
  
  //   //   
  //   const updatedPlayers = players.map((player) => {
  //     const isAssigned = temporaryCourts.some((court) =>
  //       court.players.some((courtPlayer) => courtPlayer.id === player.id)
  //     );
  //     if (isAssigned) {
  //       return {
  //         ...player,
  //         playingCount: (Number(player.playingCount) || 0) + 1
  //       };
  //     }
  //     return player;
  //   });
  
  //   setPlayers(updatedPlayers);
  
  //   //  
  //   localStorage.setItem("players", JSON.stringify(updatedPlayers));
  //   localStorage.setItem("courts", JSON.stringify(temporaryCourts));
  
  //   //   
  //   setTemporaryCourts([...courts]);
  //   setAssignClicked(false); // Confirmation  
  // }  

  function handleConfirmAssignments() {
    if (temporaryCourts.length === 0) {
      alert("Assign players first before confirming!");
      return;
    }
  
    //   (Confirmation )
    if (isSpecialEnabled) {
      currentStartIndex.current = (currentStartIndex.current + (courts.filter((court) => court.isSelected).length * 4 - specialPlayers.length)) % players.length;
    } else {
      currentStartIndex.current = (currentStartIndex.current + courts.filter((court) => court.isSelected).length * 4) % players.length;
    }
    /*
    1, 2, 
    0 ~ 5
    
    
    */
  
    //   
    const updatedPlayers = players.map((player) => {
      const isAssigned = temporaryCourts.some((court) =>
        court.players.some((courtPlayer) => courtPlayer.id === player.id && courtPlayer.name === player.name)
      );
      if (isAssigned) {
        return {
          ...player,
          playingCount: (Number(player.playingCount) || 0) + 1
        };
      }
      return player;
    });
  
    setPlayers(updatedPlayers);
    setCourts([...temporaryCourts]); // courts 
  
    //  
    localStorage.setItem("players", JSON.stringify(updatedPlayers));
    localStorage.setItem("courts", JSON.stringify(temporaryCourts));
  
    //   
    setTemporaryCourts([...temporaryCourts]);
    setAssignClicked(false); // Confirmation  
  }
  

  return (
    <div>
      <div className='grid grid-cols-[repeat(auto-fit,_minmax(150px,_1fr))] gap-2'>
        {courts.map((court, i) => (
          <div key={i}>
            <Court
              courtIndex={court.courtIndex}
              isSelected={court.isSelected}
              onCourtSelected={() => onCourtSelected(court.courtIndex)}
              players={court.players.map((player) => ({
                ...player,
                isSelected: !!selectedPlayers.find((p) => p.id === player.id) //   
              }))}
              onPlayerClick={handlePlayerClick} //   
            ></Court>
          </div>
        ))}
      </div>
      <div className='mt-4'>
        <RandomizeButton
          courts={courts}
          players={players}
          setPlayers={setPlayers}
          onAssignPlayers={onAssignPlayers}
          currentStartIndex={currentStartIndex}
          updateStartIndex={setTempStartIndex} //   
          specialPlayers={specialPlayers} //  Special List 
          setSpecialPlayers={setSpecialPlayers} //  setSpecialPlayers  
          isSpecialEnabled={isSpecialEnabled}
        ></RandomizeButton>
        {/* Change Players   */}
        <button
          onClick={handleChangePlayers}
          className='px-4 py-2 bg-green-500 text-white rounded-md ml-2'
        >
          Change Players
        </button>
        {isModalOpen && (
          <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
            <div className="bg-white p-6 rounded-lg shadow-lg w-1/3">
              <h2 className="text-lg font-bold mb-4">Change Player</h2>
              <input
                type="text"
                value={newPlayerName}
                onChange={(e) => setNewPlayerName(e.target.value)}
                placeholder="Enter player name"
                className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none"
              />
              <div className="flex justify-end space-x-4 mt-4">
                <button
                  onClick={handleModalCancel}
                  className="px-4 py-2 bg-gray-300 text-gray-700 rounded-md"
                >
                  Cancel
                </button>
                <button
                  onClick={handleModalChange}
                  className="px-4 py-2 bg-blue-500 text-white rounded-md"
                >
                  Change Player
                </button>
              </div>
            </div>
          </div>
        )}
        {/* Confirmation   */}
        <button
          onClick={handleConfirmAssignments}
          disabled={!assignClicked} // Assign players   
          className={`px-4 py-2 rounded-md ml-2 ${
            assignClicked
              ? 'bg-blue-500 text-white'
              : 'bg-gray-300 text-gray-500 cursor-default'
          }`}
        >
          Confirmation
        </button>
      </div>
    </div>
  );
}

export default Assignment;
